* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #08aeea;
  background-image: linear-gradient(#0f0c29, #302b63, #24243e);
}

a {
  text-decoration: none;
  color: unset;
}

#root {
  background-image: linear-gradient(0deg, #0c7ea8 0%, #1ab46f 100%);
  width: min(100vw, 500px);
  height: calc(100vh - 20px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.invalid {
  font-size: 14px;
  color: red;
}

.incorrect {
  background-color: rgba(255, 0, 0, 0.4) !important;
}

.correct {
  background-color: rgba(0, 255, 0, 0.3) !important;
}

.option {
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: space-between;
}
.option > h1 {
  text-align: center;
}
.option > div:nth-child(2) {
  display: flex;
  flex-direction: column;
  gap: 5px;
  max-height: 300px;
  height: 300px;
  align-items: flex-start;
  overflow-y: auto;
  overflow-x: hidden;
}
.option > div:nth-child(2) > details {
  padding-left: 20px;
}
.option > div:nth-child(2) > details summary {
  font-weight: bold;
  font-size: 18px;
}
.option > div:nth-child(2) > details summary input {
  margin-right: 5px;
}
.option > div:nth-child(2) > details > div {
  display: flex;
  gap: 5px;
  padding-left: 35px;
  margin-bottom: 2px;
}
.option > div:nth-child(2) > details > div > input {
  cursor: pointer;
}
.option > div:nth-child(2) > details > div > input:hover + p {
  scale: 1.2;
  transform: translateX(30px);
}
.option > div:nth-child(2) > details > div p {
  font-weight: bold;
  font-style: italic;
  width: 350px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.option > div:last-child {
  display: flex;
  justify-content: space-evenly;
  gap: 10px;
  flex-wrap: wrap;
}
.option > div:last-child input {
  width: 200px;
  height: 30px;
  font-weight: bold;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.5);
  cursor: pointer;
  border-radius: 4px;
}
.option > div:last-child input:active {
  transform: translateY(2px);
}

.main {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 100%;
  padding: 10px;
}
.main > div:first-child {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
}
.main > div:first-child > button {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  border-radius: 4px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: 0.3s;
  background-color: transparent;
}
.main > div:first-child > button:active {
  transform: translateX(-2px);
}
.main > div:first-child > button:hover {
  border-color: rgba(0, 0, 0, 0.3);
}
.main > div:first-child > button i {
  height: 30px;
  width: 50px;
  display: inline-block;
  background-image: url("./../images/back.svg");
  background-size: 100% 100%;
}
.main > div:first-child > p:nth-child(2) {
  font-weight: bold;
  font-size: 20px;
}
.main > div:first-child > p:last-child {
  font-weight: bold;
  font-size: 18px;
}
.main section {
  height: calc(100% - 40px);
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 3px;
}
.main section > div:first-child {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
.main section > div:first-child > p:first-child {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  background-color: rgb(241, 234, 234);
  text-transform: uppercase;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  margin-bottom: 10px;
}
.main section > div:first-child > p:nth-child(2) {
  font-size: 18px;
  text-align: justify;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  padding: 0 10px;
}
.main section > div:nth-child(2) {
  overflow-x: hidden;
  overflow-y: auto;
  height: max(50%, 250px);
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
  justify-content: space-evenly;
}
.main section > div:nth-child(2) div {
  display: flex;
  gap: 5px;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 20px;
  cursor: pointer;
}
.main section > div:nth-child(2) div span {
  font-size: 20px;
  font-weight: bold;
  border-right: 3px solid black;
  padding-right: 5px;
}
.main section > div:nth-child(2) div > p {
  text-align: center;
}

.score {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}
.score h1 {
  font-size: 48px;
}
.score p {
  font-size: 24px;
  color: darkgreen;
  font-weight: bold;
}
.score a {
  border: 1px solid rgba(0, 0, 0, 0.5);
  height: 40px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  transition: 0.3s;
}
.score a:active {
  transform: translateY(2px);
}

::-webkit-scrollbar {
  width: 5px;
  background-color: rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.5);
}

